"use client"

import { useEffect, useState } from "react"
import { BorrowRequest, getApprovedRequests } from "../../lib/api"
import { useAuth } from "../../context/AuthContext"
import { useRouter } from "next/navigation"
import Image from "next/image"

export default function BeritaAcaraPage() {
  const [approvedRequests, setApprovedRequests] = useState<BorrowRequest[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const { isAuthenticated, isLoading } = useAuth()
  const router = useRouter()

  useEffect(() => {
    if (isLoading) return // Wait for loading to complete
    
    if (!isAuthenticated) {
      router.push("/")
      return
    }
  }, [isAuthenticated, router, isLoading])

  const loadApprovedRequests = async () => {
    try {
      setLoading(true)
      setError(null)
      const data = await getApprovedRequests()
      setApprovedRequests(data)
    } catch (err: any) {
      setError(err.message || "Gagal memuat data berita acara")
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    loadApprovedRequests()
  }, [])

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('id-ID', {
      weekday: 'long',
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    })
  }

  const formatCreatedAt = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('id-ID', {
      day: 'numeric',
      month: 'long',
      year: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    })
  }

  const handlePrint = () => {
    window.print()
  }

  if (!isAuthenticated) {
    return (
      <div className="flex items-center justify-center min-h-[60vh]">
        <div className="text-center">
          <div className="w-8 h-8 border-4 border-blue-600 border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-blue-600">Memverifikasi akses...</p>
        </div>
      </div>
    )
  }

  if (loading) {
    return (
      <div className="flex items-center justify-center py-12">
        <div className="text-center space-y-4">
          <div className="w-8 h-8 border-4 border-blue-600 border-t-transparent rounded-full animate-spin mx-auto"></div>
          <p className="text-blue-600">Memuat berita acara...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="bg-gradient-to-br from-blue-50 to-yellow-50 min-h-screen -mx-4 -my-8 px-4 py-8">
      <div className="max-w-4xl mx-auto space-y-8">
        {/* Header */}
        <div className="text-center space-y-4 print:mb-8">
          <div className="flex items-center justify-center space-x-4 mb-6">
            <Image
              src="https://upload.wikimedia.org/wikipedia/commons/c/c6/Logo_Kementerian_Pekerjaan_Umum_Republik_Indonesia.svg"
              alt="Logo Kementerian PUPR"
              width={60}
              height={60}
              className="bg-white rounded-full p-2 shadow-lg print:w-12 print:h-12"
            />
            <div>
              <h1 className="text-3xl font-bold tracking-tight text-blue-900">Berita Acara</h1>
              <p className="text-lg text-blue-600 font-medium">Kementerian Pekerjaan Umum dan Perumahan Rakyat</p>
            </div>
          </div>
          <h2 className="text-xl text-blue-700 font-semibold">Peminjaman Barang dan Kendaraan Dinas</h2>
          <div className="text-sm text-blue-600 bg-white rounded-lg p-3 shadow-sm border border-blue-200">
            Dicetak pada: {new Date().toLocaleDateString('id-ID', {
              weekday: 'long',
              year: 'numeric',
              month: 'long',
              day: 'numeric',
              hour: '2-digit',
              minute: '2-digit'
            })}
          </div>
        </div>

        {/* Print Button */}
        <div className="flex justify-center print:hidden">
          <button 
            onClick={handlePrint}
            className="inline-flex items-center justify-center rounded-md text-sm font-medium transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-yellow-400 focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 bg-gradient-to-r from-blue-600 to-blue-700 text-white hover:from-blue-700 hover:to-blue-800 h-10 px-6 shadow-lg"
          >
            Cetak / Download PDF
          </button>
        </div>

        {error && (
          <div className="bg-red-50 border border-red-200 text-red-800 px-4 py-3 rounded-md print:hidden">
            <p className="text-sm font-medium">{error}</p>
            <button 
              onClick={loadApprovedRequests}
              className="mt-2 text-sm underline hover:no-underline"
            >
              Coba lagi
            </button>
          </div>
        )}

        {/* Summary Statistics */}
        <div className="bg-white border border-blue-200 rounded-xl shadow-lg p-6 print:border-0 print:shadow-none">
          <h3 className="text-lg font-semibold mb-4 text-blue-900">Ringkasan</h3>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4 text-center">
            <div className="bg-blue-50 rounded-lg p-4 border border-blue-100">
              <div className="text-3xl font-bold text-blue-900">{approvedRequests.length}</div>
              <div className="text-sm text-blue-600 font-medium">Total Permintaan Disetujui</div>
            </div>
            <div className="bg-yellow-50 rounded-lg p-4 border border-yellow-100">
              <div className="text-3xl font-bold text-blue-900">
                {new Set(approvedRequests.map(r => r.name)).size}
              </div>
              <div className="text-sm text-blue-600 font-medium">Jumlah Peminjam</div>
            </div>
            <div className="bg-green-50 rounded-lg p-4 border border-green-100">
              <div className="text-3xl font-bold text-blue-900">
                {new Set(approvedRequests.map(r => r.item)).size}
              </div>
              <div className="text-sm text-blue-600 font-medium">Jenis Barang/Kendaraan</div>
            </div>
          </div>
        </div>

        {/* Approved Requests List */}
        {approvedRequests.length === 0 ? (
          <div className="text-center py-12">
            <div className="w-16 h-16 bg-blue-100 rounded-full flex items-center justify-center mx-auto mb-4">
              <div className="w-8 h-8 bg-blue-300 rounded-full"></div>
            </div>
            <h3 className="text-lg font-medium text-blue-900 mb-2">
              Belum Ada Permintaan yang Disetujui
            </h3>
            <p className="text-blue-600">
              Berita acara akan menampilkan semua permintaan yang telah disetujui oleh approver.
            </p>
          </div>
        ) : (
          <div className="space-y-6">
            <h3 className="text-xl font-semibold text-blue-900">Daftar Permintaan yang Disetujui</h3>
            
            <div className="space-y-4">
              {approvedRequests.map((request, index) => (
                <div key={request.id} className="bg-white border border-blue-200 rounded-xl shadow-lg p-6 print:border print:border-gray-300 print:shadow-none">
                  <div className="flex items-start justify-between mb-4">
                    <div>
                      <h4 className="text-lg font-semibold text-blue-900">
                        {index + 1}. {request.name}
                      </h4>
                      <p className="text-sm text-blue-600">
                        Diajukan: {formatCreatedAt(request.createdAt)}
                      </p>
                    </div>
                    <span className="inline-flex items-center rounded-full bg-green-100 px-3 py-1 text-sm font-medium text-green-800 ring-1 ring-inset ring-green-600/20 print:bg-transparent print:ring-gray-400">
                      Disetujui
                    </span>
                  </div>

                  <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                    <div className="space-y-3">
                      <div className="bg-blue-50 p-3 rounded-lg">
                        <p className="text-sm font-medium text-blue-800">Barang/Kendaraan</p>
                        <p className="text-sm text-blue-900 font-semibold">{request.item}</p>
                      </div>
                      <div className="bg-yellow-50 p-3 rounded-lg">
                        <p className="text-sm font-medium text-blue-800">Tanggal Peminjaman</p>
                        <p className="text-sm text-blue-900 font-semibold">{formatDate(request.date)}</p>
                      </div>
                    </div>
                    
                    <div>
                      <p className="text-sm font-medium text-blue-800 mb-2">Keperluan Dinas</p>
                      <p className="text-sm text-blue-900 bg-blue-50 p-3 rounded-lg border border-blue-100 print:bg-gray-50 print:border print:border-gray-200">
                        {request.purpose}
                      </p>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        )}

        {/* Footer */}
        <div className="border-t border-blue-200 pt-6 print:border-gray-300">
          <div className="text-center text-sm text-blue-600 space-y-2 bg-white rounded-lg p-4 shadow-sm border border-blue-200">
            <p className="font-medium">Dokumen ini dibuat secara otomatis oleh sistem Nota Dinas</p>
            <p>Kementerian Pekerjaan Umum dan Perumahan Rakyat</p>
            <p>Untuk informasi lebih lanjut, hubungi administrator sistem</p>
          </div>
        </div>

        {/* Print Styles */}
        <style jsx global>{`
          @media print {
            body {
              margin: 2cm;
              font-family: "Times New Roman", Times, serif;
              font-size: 12pt;
              color: black;
              background: white;
              print-color-adjust: exact;
              -webkit-print-color-adjust: exact;
            }
            .print\\:hidden {
              display: none !important;
            }
            .print\\:border {
              border: 1px solid black !important;
            }
            .print\\:border-gray-300 {
              border-color: black !important;
            }
            .print\\:shadow-none {
              box-shadow: none !important;
            }
            .print\\:bg-transparent {
              background-color: transparent !important;
            }
            .print\\:ring-gray-400 {
              --tw-ring-color: black !important;
            }
            .print\\:bg-gray-50 {
              background-color: white !important;
            }
            .print\\:border-gray-200 {
              border-color: black !important;
            }
            .print\\:mb-8 {
              margin-bottom: 2rem !important;
            }
            .print\\:w-12 {
              width: 3rem !important;
            }
            .print\\:h-12 {
              height: 3rem !important;
            }
            h1, h2, h3, h4, h5, h6 {
              page-break-after: avoid;
              font-weight: bold;
            }
            p {
              orphans: 3;
              widows: 3;
              line-height: 1.5;
            }
            .page-break {
              page-break-after: always;
            }
            .no-break {
              page-break-inside: avoid;
            }
            .header, .footer {
              text-align: center;
              font-size: 10pt;
              color: black;
            }
            .header {
              margin-bottom: 1cm;
            }
            .footer {
              margin-top: 1cm;
            }
            table {
              width: 100%;
              border-collapse: collapse;
              margin-bottom: 1cm;
            }
            th, td {
              border: 1px solid black;
              padding: 0.3em 0.5em;
              text-align: left;
            }
            th {
              background-color: #ddd;
            }
          }
        `}</style>
      </div>
    </div>
  )
}
